<template>
	<div>
		<div class="picture_box">
			<div class="picture_btn">
				<span class="picture_con" v-if="removebtn" @click="remove()">批量删除</span>
				<p class="picture_p" v-if="shows">
					<span class="cancel" @click="cancel()">取消</span>
					<span class="ensure" @click="ensure()">确定</span>
				</p>
			</div>
			<ul class="picture_ul">
				<li v-for="(item,index) in list " class="picture_li" @click="Checkovers(index)">
					<img :src="item.image"/>
					<div v-if="item.show" class="checkOver"></div>
				</li>
			</ul>
		</div>
		
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				removebtn:true,
				shows:false,
				list:[	{image:"../../../static/img51.jpg",show:false},
						{image:"../../../static/img51.jpg",show:false},
						{image:"../../../static/img51.jpg",show:false},
						{image:"../../../static/img51.jpg",show:false},
						{image:"../../../static/img51.jpg",show:false},
						{image:"../../../static/img51.jpg",show:false},
						{image:"../../../static/img51.jpg",show:false}
					]
			}
		},
		methods:{
			remove(){
				this.shows=true;
				this.removebtn=false;
			},
			cancel(){
				this.shows=false;
				this.removebtn=true;
				for(var i=0;i<this.list.length;i++){
					this.list[i].show=false;
				}
			},
			ensure(){
				this.$popup({
					title: '提示',
          			content: '确定删除这n张照片吗？'
				}).then((res)=>{
					console.log(res)
					if(res=='sure'){
						return;
					}
				})
			},
			Checkovers(index){
				if(this.shows==true){
					this.list[index].show=!this.list[index].show
				}else{
					this.$router.push({name: 'Viewphotos'})
				}
			}
		}
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	
	.picture_box{
		width: 100%;height: auto;
		.picture_btn{
			width:100%;height:0.48rem;line-height: 0.48rem;padding-top:0.08rem;
			.picture_con{font-size: 0.3rem;float: right;margin-right: 0.3rem;}
			.picture_p{
				font-size: 0.3rem;padding: 0 0.3rem;
				.cancel{float: left;}
				.ensure{float: right;}
			}
		}
		.picture_ul{
			width: 100%;height: auto;display: flex;flex-wrap: wrap;
			.picture_li{
				width: 2.22rem;height: 2.22rem;margin-left:0.2rem;margin-top:0.2rem;position: relative;
				img{
					width: 100%;height: 100%;
				}
				.checkOver {position: absolute; left: 0; top: 0; width: 100%; height: 100%; background: url("../../../static/checkbox-on.png") 1.7rem 1.7rem no-repeat rgba(255, 255, 255, 0.2); background-size: .44rem .44rem; }
			}
		}
	}
</style>


